import uniIcon from '@/components/uni-icon/uni-icon.vue';
export default {
	components: {
		uniIcon
	},
	name: 'Bugbtn',
	data () {
		return {};
	},
	props: {
		value: {
			default: 0
		},
		max: {
			default: 10
		}
	},
	watch: {
		value (val) {
			this.initValue();
		}
	},
	onLoad() {
		this.initValue();
	},
	methods: {
		initValue () {
			if (this.value !== 0 && !this.value) {
				this.value = 0;
			}
			console.log(value);
		},
		clkMinus () {
			this.value > 0 && (this.value -= 1);
			this.$emit('click', {
				'num': this.value
			});
		},
		clkPlus () {
			if (this.value < this.max) {
				this.value += 1;
				this.$emit('click', {
					'num': this.value
				});
			} else {
				this.$emit('click', {
					'error': '已达上限'
				});
			}
		}
	}
}
